        .col-12.col-md-6.col-lg-6.bg-white.ms-md-2
          p La capacidad de comunicacin del personal dedicado al turismo resulta imprescindible para establecer una buena relacin con los clientes y para dar una buena imagen general del establecimiento o servicio que se ofrece.
    
    h2.h3#t_3_1 3.1 Definicin
    p La comunicacin es un proceso que consiste en la transmisin e intercambio de mensajes entre un emisor y un receptor. La palabra deriva del latn comunicacin que significa compartir, participar en algo o poner en comn.
    p Los elementos que participan son los siguientes:

    //- Tarjetas definicion
    .tarjeta--color-curso-i.px-4.pt-4.pt-md-1.mb-4
        .row.py-md-3.align-items-center
          .col-12.col-md-3.col-lg-2.d-flex.align-items-center
            img.normal(src="@/assets/curso/tema3/emisor.png")
          .col
            p <strong>Emisor:</strong> Es quien transmite el mensaje.

    .tarjeta--color-curso-i.px-4.pt-4.pt-md-1.mb-4
        .row.py-md-3.align-items-center
          .col-12.col-md-3.col-lg-2.d-flex.align-items-center
            img.normal(src="@/assets/curso/tema3/receptor.png")
          .col
            p <strong>Receptor:</strong> Es el que recibe el mensaje.

    .tarjeta--color-curso-i.px-4.pt-4.pt-md-1.mb-4
        .row.py-md-3.align-items-center
          .col-12.col-md-3.col-lg-2.d-flex.align-items-center
            img.normal(src="@/assets/curso/tema3/codigo.png")
          .col
            p <strong>Cdigo:</strong> es el conjunto de signos que sern utilizados para crear el mensaje (palabras, gestos, smbolos).

    .tarjeta--color-curso-i.px-4.pt-4.pt-md-1.mb-4
        .row.py-md-3.align-items-center
          .col-12.col-md-3.col-lg-2.d-flex.align-items-center
            img.normal(src="@/assets/curso/tema3/mensaje.png")
          .col
            p <strong>Mensaje:</strong> es la informacin o conjunto de datos que se transmiten.

    .tarjeta--color-curso-i.px-4.pt-4.pt-md-1.mb-4
        .row.py-md-3.align-items-center
          .col-12.col-md-3.col-lg-2.d-flex.align-items-center
            img.normal(src="@/assets/curso/tema3/canal.png")
          .col
            p <strong>Canal de comunicacin:</strong>  es el medio fsico que se utiliza para enviar el mensaje.  Ejemplo : telfono, televisin, internet, el propio aire, etc.

    .tarjeta--color-curso-i.px-4.pt-4.pt-md-1.mb-4
        .row.py-md-3.align-items-center
          .col-12.col-md-3.col-lg-2.d-flex.align-items-center
            img.normal(src="@/assets/curso/tema3/ruido.png")
          .col
            p <strong>Ruido:</strong>  son todas las distorsiones que pueden influir en la recepcin del mensaje original, y pueden ser tanto del emisor, como del canal o del receptor.

    .tarjeta--color-curso-i.px-4.pt-4.pt-md-1.mb-4
        .row.py-md-3.align-items-center
          .col-12.col-md-3.col-lg-2.d-flex.align-items-center
            img.normal(src="@/assets/curso/tema3/retroalimentacion.png")
          .col
            p <strong>Retroalimentacin o feedback:</strong>  en una primera instancia, es la respuesta del receptor hacia el mensaje recibido. Si posteriormente el emisor responde a lo enviado por el receptor, tambin se considera retroalimentacin.

    .tarjeta--color-curso-i.px-4.pt-4.pt-md-1.mb-4
        .row.py-md-3
          .col-12.col-md-3.col-lg-2.d-flex.align-items-center
            img.normal(src="@/assets/curso/tema3/contexto.png")
          .col
            p <strong>Contexto:</strong>  son las circunstancias en las que se desarrolla el proceso de comunicacin. Tienen influencia directa en la interpretacin del mensaje (el lugar en donde se desarrolla la comunicacin, la motivacin de la comunicacin, contexto social, etc.)
    p El proceso comunicativo es esencial para la vida en sociedad: permite que los seres humanos se expresen y compartan informacin entre s, establezcan relaciones, lleguen a acuerdos y sean capaces de organizarse.

    .tarjeta--color-curso-h.fs-5.fw-bold.my-5.px-3.px-md-5.py-4
      .row.align-items-center
        .col-12.col-sm.text-center.text-md-start
          span Ral Villanueva Lpez (2017) Comunicacin y atencin al cliente en hostelera y turismo.
        .col-12.col-md.text-center.text-md-end
          a.boton--color-sistema-a.rounded-pill.px-5.mt-4.mt-md-0
            span.fw-bold Ebooks aqu

    h2.h3.pt-5#t_3_2 3.2. Tipos de comunicacin.
    .tarjeta.tipos-background.p-5.my-4
      .row
        .col-12.col-lg-6
          p.fs-4 La comunicacin puede llevarse a cabo de diferentes maneras:<br><strong>Verbal</strong> utiliza un lenguaje o idioma.<br><strong>No verbal</strong> se vale de gestos, lenguaje corporal o signo no lingsticos.

    h3.h5 3.2.1. Lenguaje verbal
    p Se refiere a la comunicacin que se vale de la palabra para dar el mensaje, es la principal forma de comunicacin que se utiliza. Puede ser oral o escrita

    .row
      .col-12.col-lg.me-lg-5
        h4.h5 3.2.1.1. Comunicacin oral:
        figure.my-4
          img(src="@/assets/curso/tema3/oral.jpg")
        p es el intercambio de mensajes a travs del habla. La voz juega un papel muy importante en nuestro contacto con el cliente.  Debemos ser directos al hablar. Evitar <strong>"muletillas o gaguear"</strong> Y sobre todo, evitar la voz postiza o fingida. La presentacin personal, el espacio adecuado para atender al cliente y mantener un contacto visual.
      .col-12.col-lg.ms-lg-5
        h4.h5 3.2.1.2. Comunicacin escrita:
        figure.my-4
          img(src="@/assets/curso/tema3/escrita.jpg")
        p en este caso, el proceso comunicacional ocurre a travs del lenguaje escrito.

        p Ejemplo : carta, mensajes de texto, correos electrnicos. 

        p El contenido del mensaje es  riguroso y preciso, lgico y claro aplicando  normas gramaticales y reglas de acentuacin.

    h3.h5 3.2.2. Lenguaje no verbal
      p Son todos los gestos que consciente e inconscientemente transmitimos en nuestras relaciones interpersonales y que dan cuenta de nuestras posiciones acerca de lo que el otro dice o hace.  <strong>Se expresa a travs del lenguaje corporal, la proximidad, signos no lingsticos y sonidos sin palabras.</strong>

    h2.h3#t_3_3 3.3. Tcnicas

    Tabs
      div(titulo="Generar confianza")
        .row.align-items-center.p-3.pd-lg-5
          .col-12.col-lg-6.p-md-5.text-center.p-3
            img(src="@/assets/curso/tema3/tab1.jpg")
          .col-12.col-lg-6.p-md-5
            .h2 A 
            p Para que se d una buena comunicacin y generar confianza con el oyente se debe tener conviccin, el emisor debe estar convencido y creer lo que est diciendo, de no ser as es posible que el mensaje no llegue al receptor como se piensa y de lo contrario sea interpretado de manera errnea, y no se lograr el objetivo.

      div(titulo="Actitud de escucha")
        .row.align-items-center.p-3.pd-lg-5
          .col-12.col-lg-6.p-md-5.text-center.p-3
            img(src="@/assets/curso/tema3/tab2.jpg")
          .col-12.col-lg-6.p-md-5
            .h2 B
            p Actitud de escucha, es la principal herramienta.  La escucha activa consiste en una forma de comunicacin, que demuestra al hablante, que el oyente le est entendiendo.

      div(titulo="Postura y movimientos")
        .row.align-items-center.p-3.pd-lg-5
          .col-12.col-lg-6.p-md-5.text-center.p-3
            img(src="@/assets/curso/tema3/tab3.jpg")
          .col-12.col-lg-6.p-md-5
            .h2 C
            p Postura y movimientos.  La posicin fsica del cuerpo es un reflejo de la  condicin mental y del estado de nimo en el que se encuentra, por esto  influye en la percepcin que el o los oyentes pueden tener.
            ul
              li Aprender a moverse con naturalidad y moderadamente.
              li Tener una postura recta y a la vez armoniosa.
              li Las manos deben usarse con movimientos moderados reforzando el contenido del mensaje.
              li Cada gesto o movimiento debe apoyar el significado de lo que se est comunicando, guardar concordancia entre lo que se expresa y lo que hace el cuerpo.

      div(titulo="Los gestos faciales")
        .row.align-items-center.p-3.pd-lg-5
          .col-12.col-lg-6.p-md-5.text-center.p-3
            img(src="@/assets/curso/tema3/tab4.jpg")
          .col-12.col-lg-6.p-md-5
            .h2 D
            p Los gestos faciales generan gran impacto al momento de comunicar, se debe aprender a apoyar el mensaje con la expresin facial.
            ul
              li Mostrar una expresin cordial.
              li Controlar gestos negativos como inseguridad o nerviosismo.
              li Ser moderados con la expresividad.
              li Es importante sonrer, de lo contrario el cliente puede percibir hostilidad.
              li Evitar fingir porque se puede mostrar con el lenguaje no verbal y perder credibilidad.
              li Se debe establecer contacto visual y la mirada debe reflejar conviccin y entusiasmo, esto traslada credibilidad al mensaje.
            p La expresin debe reflejar: cortesa, amabilidad, inters, confianza.

    h2.h3#t_3_4 3.4 Comunicacin efectiva
      .row
        .col-12.col-md-6.my-3.my-md-0
          p Busca que la persona que emite el mensaje lo realice de forma clara, directa y que no quede lugar a confusiones o malos entendidos, busca que la persona que recibe el mensaje procese la informacin en trminos que pueda entender y dar retroalimentacin sobre el mensaje que ha comprendido. Se resume en que lo valioso del proceso no es lo que se dice, sino lo que entiende el interlocutor. 
          p La comunicacin asertiva es aquella en la que el emisor logra expresar un mensaje de forma simple, oportuna y clara, considerando las necesidades del receptor.  Implica respeto hacia uno mismo al expresar sus ideas y defenderlas y respeto hacia los dems. Se trata de una habilidad social importante asociada a la inteligencia emocional y a la comunicacin no verbal.

        .col-12.col-md-6.my-3.my-md-0
          figure
            img(src="@/assets/curso/tema3/comunicacion1.jpg")

        .col-12.col-md-6.my-3.my-md-0
          figure
            img(src="@/assets/curso/tema3/comunicacion2.jpg")

        .col-12.col-md-6.my-3.my-md-0
          p Tambin est la comunicacin agresiva que aparece cuando se mantiene una defensa de los derechos personales y opiniones propias por encima de todo y de una manera inapropiada y exigente, vulnerando los derechos de las otras personas, y 
          p La pasiva, en la cual se vulneran los propios derechos al no ser capaz de expresar libremente las ideas, opiniones o sentimientos, lo que conlleva a no ser escuchados o que se le preste atencin
          p Por lo tanto la asertividad es la herramienta a utilizar cuando de comunicarse se trata, se puede aprender mediante la prctica, lo que har que las comunicaciones sean ms positivas.

    p.h4.mt-5 PROTOCOLO DE COMUNICACIN EN LA ATENCIN
    p La gua de protocolo de comunicacin tiene como objetivo establecer normas en  la comunicacin con el cliente, a fin de generar un ambiente de cordialidad, cortesa y amabilidad. Siempre mirarlo a los ojos y resolver inmediatamente su solicitud.  En caso de tener dudas, ser recursivos buscando la ayuda de un compaero de trabajo, siempre tomar la iniciativa para saludarlo.
 
    p Detalles que el cliente considera positivo:

    .row
      .col-12.col-md-6
        ul
          li La amabilidad
          li La correccin
          li El respeto
          li El buen trato
          li La humildad
          li La profesionalidad
          li La limpieza
          li Las palabras justas en el momento exacto y oportuno

      .col-12.col-md-6
        blockquote.text-end.fs-1.fw-light.p-3.p-lg-5
          .me-5.pe-4.pe-lg-0 Siempre mirarlo a los ojos y resolver inmediatamente su solicitud.

    h2.h3#t_3_5.mt-5.pt-5 3.5 Tipos de comunicacin
    h3.h5 3.5.1 Comunicacin telefnica.
    .row.justify-content-between.my-5
      .col-12.col-md-4
        img(src="@/assets/curso/tema3/comunicacion-telefonica.jpg")
      .col-12.col-md-7
        p Los clientes no ven al interlocutor y es muy fcil generar una mala interpretacin por parte del cliente, por tal la importancia de <strong>atender con profesionalidad y eficacia cualquier</strong> llamada. 
        p En la hotelera el personal debe estar preparado para tratar a todos de la manera ms profesional, con prontitud, dar respuestas convenientes y solucionar dudas y problemas que tengan los clientes,  y no olvidar que su objetivo principal es <strong>"vender servicios"</strong>.
        p Dentro de las recomendaciones se encuentra tener siempre a mano papel y bolgrafo para tomar notas, <strong>tener la informacin que se va a proporcionar, estar ubicado en un espacio con la menor interferencia posible</strong>, conocer el nombre de los clientes, contar con una base de datos que proporciones informacin del cliente cuando llame, un listado de extensiones en caso de requerir transferir la llamada, etc.

    .row.justify-content-between
      .col-12.col-md-6.pe-md-5
        .bg-azul-claro.border-radius-20
          figure
            img(src="@/assets/curso/tema3/gestion-tiempo.png")
          p.px-4.pb-4 <strong>Gestin del tiempo</strong>. Hablar de forma clara, concisa y confirmando lo que se habla. Evitar largas esperas o largas conversaciones y considerar retornar la llamada cuando no se tiene respuesta inmediata. 
      .col-12.col-md-6.ps-md-5
        .bg-azul-claro.border-radius-20
          figure
            img(src="@/assets/curso/tema3/sonreir-escuchar.png")
          p.px-4.pb-4 <strong>Sonrer y escuchar</strong>. Un trato amable provoca que la otra parte se siente cmoda y distendida y la conversacin sea mucho ms productiva.  La sonrisa por tratarse de un gesto propio se transmite a travs del telfono.

    .tarjeta-numerada
      .tarjeta-numerada__numero-circulo
        span.bg-color-curso-i 1
      .tarjeta-numerada__contenido.bg-azul-claro
        .row
          .col-12.col-md-3
            img.auto(src="@/assets/curso/tema3/conflicto.png")
          .col-12.col-md
            p <strong>Resolver conflictos.</strong> En ocasiones los clientes llaman porque necesitan resolver un conflicto, de nada sirve eludirlos porque los problemas no se solucionan solos y lo que puede generar es aadir un cliente enfadado.

    .tarjeta-numerada
      .tarjeta-numerada__numero-circulo
        span.bg-color-curso-i 2
      .tarjeta-numerada__contenido.bg-azul-claro
        .row
          .col-12.col-md-3
            img.auto(src="@/assets/curso/tema3/privacidad-online.png")
          .col-12.col-md
            p <strong>Discresin.</strong> Se debe ser discreto y mantener la privacidad de los clientes, no escuchar conversaciones ajenas, ni divulgar conversaciones mantenidas con los clientes ya que se consideran privadas. 

    .tarjeta-numerada
      .tarjeta-numerada__numero-circulo
        span.bg-color-curso-i 3
      .tarjeta-numerada__contenido.bg-azul-claro
        .row
          .col-12.col-md-3
            img.auto(src="@/assets/curso/tema3/mensaje-de-voz.png")
          .col-12.col-md
            p <strong>Voz y lenguaje.</strong>  La voz tiene que ser clara y sonriente, el lenguaje preciso y adaptado al cliente y expresarse con educacin y simpata.

    .tarjeta-numerada
      .tarjeta-numerada__numero-circulo
        span.bg-color-curso-i 4
      .tarjeta-numerada__contenido.bg-azul-claro
        .row
          .col-12.col-md-3
            img.auto(src="@/assets/curso/tema3/auriculares.png")
          .col-12.col-md
            p <strong>Reglas para el buen uso del telfono.</strong>
            ul
              li Saludo al iniciar la conversacin.
 
              li No dejar repicar ms de cuatro timbres.

              li Presentarse identificndose as mismo y la empresa en la que trabaja.

              li Sonrer al hablar.

              li Preguntar el nombre completo del cliente.

              li Escuchar con atencin sin atender otras cosas a la vez.

              li Preguntar todo lo necesario para entender el mensaje correctamente.

              li En caso de interrumpir por cualquier razn la llamada se debe disculpar y reanudar la conversacin con amabilidad.

              li No comer ni beber mientras se habla.

              li Llamar al cliente por su nombre, con respeto.

              li En caso de no poder dar respuesta inmediata, dejarlo saber al cliente e informarle plazo aproximado para resolucin del mismo.
 
              li No actuar como oyente pasivo, no interrumpir a la otra persona mientras est hablando, intercalar expresiones que le hagan entender que est atento.

              li Mantener informado al cliente de lo que se est haciendo para solucionar su problema y no dejarlo en espera sin informarle.

              li Hablar con claridad, pausadamente y vocalizando.

              li Variar el tono de voz para que no resulte demasiado montono y plano.

              li Utilizar un lenguaje claro, cortes, sin modismos ni tecnicismos.

              li Asegurarse de que el interlocutor no oye interferencias o sonidos raros.

              li No tutee al cliente a no ser, de permiso para ello.

              li Si un cliente deja un mensaje en el contestador, se le deber devolver la llamada lo antes posible.

              li Al despedirse hacerlos con amabilidad y con expresiones corteses de despedida.

    .tarjeta-numerada
      .tarjeta-numerada__numero-circulo
        span.bg-color-curso-i 5
      .tarjeta-numerada__contenido.bg-azul-claro
        .row
          .col-12.col-md-3
            img.auto(src="@/assets/curso/tema3/text.png")
          .col-12.col-md
            p <strong>Mensajes.</strong>  En caso de recibir una llamada para un tercero, se debe tomar nota del mensaje y procesarlo a la mayor brevedad. <strong>El mensaje debe ser  claro y asegurarse que se tienen todos los datos</strong>, principalmente el nombre del destinatario del mensaje, de la persona que deja el mensaje y nmero de telfono. 


    h4.h5.my-5.pt-4 3.5.2.1 Clasificacin segn la explotacin de los datos.

    .row
      .col-12.col-md-4.d-flex.align-items-stretch.my-3.my-lg-0
        .tarjeta.tarjeta__template--azul-claro.p-4
          figure(style="height:51px")
            img.auto(src="@/assets/curso/tema3/transferir1.png")
          p.h6 Unidireccional.
          p La transmisin se realiza en un solo sentido ejemplo: <strong>"una oferta promocional desde el rea de reservas"</strong>.

      .col-12.col-md-4.d-flex.align-items-stretch.my-3.my-lg-0
        .tarjeta.tarjeta__template--azul-claro.p-4
          figure(style="height:51px")
            img.auto(src="@/assets/curso/tema3/transferir2.png")
          p.h6 Bidireccional.
          p Cuando se produce una comunicacin de emisor a receptor y de receptor a emisor. <strong>Ejemplo:</strong> solicitud de reserva de parte del cliente y respuesta del rea de reservas.
          
      .col-12.col-md-4.d-flex.align-items-stretch.my-3.my-lg-0
        .tarjeta.tarjeta__template--azul-claro.p-4
          figure
            img.auto(src="@/assets/curso/tema3/transferir3.png")
          p.h6 Bidireccional simultanea.
          p Donde el emisor y el receptor intercambian papeles continuamente. <strong>Ejemplo:</strong> solicitud que requiere respuesta inmediata.

    h4.h5.mt-5.mb-4 3.5.2.2 Clasificacin segn la trasmisin
    .row
      .col-12.col-md-6.my-3.my-lg-0
        .tarjeta--color-curso-j.px-5.py-3
          .row.align-items-center
            .col-4
              img.auto(src="@/assets/curso/tema3/video-conferencia.png")
            .col-8
              p <strong>Sincrnica:</strong><br/>chat, videoconferencias.
      .col-12.col-md-6.my-3.my-lg-0
        .tarjeta--color-curso-j.px-5.py-3
          .row.align-items-center
            .col-4
              img.auto(src="@/assets/curso/tema3/ordenador.png")
            .col-8
              p <strong>Asincrnica:</strong><br/>correo electrnico, foros.

    h4.h5.mt-5.mb-4 3.5.2.3 Tcnicas de comunicacin telemtica

    .tarjeta.tarjeta__template--azul-claro.my-4
      .row
        .col-3.d-flex.align-items-stretch
          span.d-inline-block.bg-color-curso-g.px-4(style="color: #FFF1DB") .
        .col
          p <strong>Tema del mensaje:</strong><br/> exponer de manera breve el mensaje.

    .tarjeta.tarjeta__template--azul-claro.my-4
      .row
        .col-3.d-flex.align-items-stretch
          span.d-inline-block.bg-color-curso-g.px-4(style="color: #FFF1DB") .
        .col
          p <strong>Cuerpo del mensaje:</strong><br/> sigue el procedimiento de una carta.
          ul
            li Saludo.
            li Texto del mensaje.
            li Despedida con firma.

    Slider#slider-tema-3
      .row.justify-content-between
        .col-12.col-md-4.text-end
          p.h2.ps-4.lh-sm La revolucin de la hotelera moderna
          p Vino con la Revolucin industrial y la generalizacin de los medios de transporte que permiti a las personas desplazarse de forma masiva de un lugar a otro. Por aquel entonces, <strong>los viajes estaban reservados a clases ms adineradas que podan salir de vacaciones y por tanto exigan alojamientos acorde a su nivel econmico</strong>. Se generalizan establecimientos para comer y pernoctar, y surge as lo que conocemos como hotelera moderna.
        .col-12.col-md-8
          figure.ps-5
            img(src="@/assets/curso/tema3/slide1.jpg")

      .row.justify-content-between
        .col-12.col-md-4.text-end
          p.h2.ps-4.lh-sm La revolucin de la hotelera moderna
          p El primer hotel de concepcin moderna en 1807 fue el <strong>"Badische Hof"</strong>, construido en Alemania, comenz con 18 columnas dricas que se extendan a lo largo de tres pisos, lo que hizo que el comedor fuera el centro de atencin y con algunos aspectos novedosos para la poca como sala de lectura o biblioteca.
        .col-12.col-md-8
          figure.ps-5
            img(src="@/assets/curso/tema3/slide2.jpg")

      .row.justify-content-between
        .col-12.col-md-4.text-end
          p.h2.ps-4.lh-sm La revolucin de la hotelera moderna
          p Con la aparicin del ferrocarril, tambin surgen los hoteles "trmino" para dar alojamiento a los viajeros al lado de las estaciones.
        .col-12.col-md-8
          figure.ps-5
            img(src="@/assets/curso/tema3/slide3.jpg")

      .row.justify-content-between
        .col-12.col-md-4.text-end
